<template>
  <div class='videothing'>
    <p class='content' align='left' v-on:mouseover='contact = true' @mouseleave='contact=false'>
    <button>
      CONTACT
    </button>
    <contact-info v-bind:show='contact'>
    </contact-info>
    </p>
    <p id="verticalSpace">
    </p>
    <youtube video-id="R6FdV8stOsE">
    </youtube>
  </div>
</template>

<script>
import VueYoutubeEmbed from 'vue-youtube-embed'
import Vue from 'vue'
import ContactInfo from '@/components/ContactInfo'

Vue.use(VueYoutubeEmbed)

export default {
  name: 'video-component',
  data () {
    return {
      contact: false
    }
  },
  components: {
    'contact-info': ContactInfo
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

@media screen and (min-width: 300px) {
  .content {
    position: absolute;
    left: 4%;
    top: 5%;
  }
}

button {
  font-family: 'Times New Roman', Times, serif;
  position: relative;
  text-align: left;
  font-size: xx-large;
  background-color: rgba(255, 100, 255, 0.0);
  padding: 1%;
  border: none;
  display: inline;
  color: #2c3e50;
  outline: none;
}
#verticalSpace {
  margin-top: 15%;
}
</style>



