<template>
  <transition name='slide-fade'>
  <div align=left v-if='show' class='contact'>
  <p>
  contact and booking:
  </p>
  <p margin-left='10%'>
    <a href="mailto:connelly.mct@gmail.com">
  connelly.mct@gmail.com
  </a>
  </p>
  <p>
  listen:
  </p>
  <iframe allowtransparency="true"
    scrolling="no" frameborder="no"
    src="https://w.soundcloud.com/icon/?url=http%3A%2F%2Fsoundcloud.com%2Fclarissaconnelly&color=black_white&size=32"
    style="width: 32px; height: 32px;"></iframe>,
      <a href="https://clarissaconnelly.bandcamp.com">
        <img src='../assets/bandcamp-logotype-dark-32.png'>
      </a>
  </div>
  </transition>
</template>

<script>
export default {
  name: 'contact-info',
  data () {
    return {
    }
  },
  props: [
    'show'
  ]
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
p {
  font-size: x-large
}
contact {
  float: left;
  font-size: x-large;
  display: inline;
  clear: both;
  text-align: left;
  z-index:4000;
  margin-bottom: -400px;
}
.slide-fade-enter-active, .slide-fade-leave-active {
  transition: opacity 2s;
}

.slide-fade-enter, .slide-fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  transform: translateX(10px);
  opacity: 0;
}
</style>



