<template>
  <div>
    <video id="video" muted loop>
      <source src='../assets/background.mp4' type='video/mp4'>
    </video>
  </div>
</template>

<script>
export default {
  name: 'background-video',
  data () {
    return {
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
video {
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
  z-index: -100;
}
</style>



